<template>
  <div class="course-live-play">
    <head-top :title="resourceInfo.title || ''"></head-top>

    <div v-if="videoItem">
      <vue-video ref="video" :auto-play="true" :media-src="videoItem ? videoItem.playPath : ''"
                 :media-poster="videoItem ? videoItem.playPoster : ''"></vue-video>
      <div class="video_info">
        <div class="flex-between info_title">
          <span>{{ resourceInfo.remark || '' }}</span>
        </div>
        <div class="flex-between info_mark">
          <van-tag mark type="success">{{ resourceInfo.title }}</van-tag>
          <div v-if="videoItem">{{ videoItem["MaterialLastAccessDate"] |
                                formatDate(videoItem["MaterialLastAccessDate"])}}
          </div>
        </div>
      </div>
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <div v-if="restList" class="rest_list">
        <div v-for="(item, index) in restList" :key="index" class="line_mark"
             @click="select(item)">
          <img :src="item.playPoster" :onerror="defaultImg" alt=""/>
          <div class="line_right">
            <van-tag mark type="success">{{ resourceInfo.title }}</van-tag>
            <div class="line_info"> {{ resourceInfo.remark || '' }}</div>
            <div>{{ item["MaterialLastAccessDate"] | formatDate(item["MaterialLastAccessDate"])}}</div>

          </div>
        </div>
      </div>
      <r-lack-data v-if="!liveList || liveList.length === 0"></r-lack-data>
    </van-pull-refresh>

  </div>
</template>

<script>
import VueVideo from "@/components/r-video/vue-video";

export default {
  name: "",
  components: { VueVideo },
  props: {},
  data () {
    return {
      defaultImg: 'this.src="' + require('@/assets/video/videopost.png') + '"', //
      resourceInfo: this.$route.query,
      typeOptions: [],
      typeSelectedIndex: 0,
      typeOptionsShow: false,
      typeValue: "",

      isLoading: false,

      selectIndex: -1,
      liveList: [],
      videoItem: null
    };
  },
  computed: {
    restList () {
      return this.liveList.filter(r => r.index !== this.selectIndex);
    }
  },
  mounted () {
    this.onSearch();
  },
  methods: {
    onSearch () {
      this.dataList = [];
      this.liveList = [];
      this.isLoading = true;
      this.$mainHttp.recordLesson.dataPlayList({
        rtID: this.resourceInfo.id || ""
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["MaterialCreateDate"] > b["MaterialCreateDate"] ? 1 : -1;
          });

          res.Datas.forEach((item, index) => {
            const playPath = `http://${this.$store.getters.serverConfig["LiveHost"]}:${this.$store.getters.serverConfig["LivePort"]}/vod${item["MaterialStorePath"]}`;
            const playPoster = playPath.replace(".mp4", ".png").replace(/\\/g, "/");
            const sub = {
              index,
              ...item,
              playPath,
              playPoster
            };
            this.liveList.push(sub);
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        if (this.liveList.length >= 1) this.select(this.liveList[0]);
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    select (item) {
      this.selectIndex = item.index;
      this.videoItem = this.liveList && this.liveList.length > 0 ? this.liveList[this.selectIndex] : null;
      if (this.videoItem) {
        setTimeout(() => {
          this.$refs["video"].initParam(this.videoItem.playPath, this.videoItem.playPoster);
        }, 0);

        setTimeout(() => {
          window.scrollTo(0, 0);
        }, 50);
      }
    },
    tabChange (item) {
      this.typeSelectedIndex = item.index;
    },
    handle (val) {
      this.typeValue = this.typeOptions[val].value;
      setTimeout(() => {
        this.typeOptionsShow = false;
      }, 200);
    }
  }
};
</script>

<style lang="less">
@import url("./../../styles/common/base.less");

.course-live-play {
  /*margin-bottom: 50px;*/
  background-color: #fff;

  .video_info {
    text-align: left;
    font-size: @font-info;
    line-height: 1.5;
    padding: 2vw 3vw;

    .info_title {
      word-break: break-all;
      word-wrap: break-word;
    }

    .info_mark {
      margin-top: 1vw;
    }
  }

  .video-player {
    width: 100vw;
    min-height: 55vw;
    margin: 0 auto;
    position: relative;
    overflow: hidden;

    video {
      width: 100%;
      margin: 0 auto;
    }
  }

  .rest_list {
    padding-top: 2vw;
    border-top: 1px solid #f1f1f1;

    .line_mark {
      position: relative;
      display: flex;
      justify-content: start;
      margin: 0 3vw;
      font-size: @font-info;
      line-height: 1.5;
      color: #777;
      padding: 2vw 0;

      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-top: 1px solid #f0f1f1;
        transform: scaleY(0.6);
      }

      img {
        max-width: 30vw;
        max-height: 20vw;
        padding-right: 4vw;
      }

      .line_right {
        text-align: left;
        word-break: break-all;
        margin-top: 1.5vw;

        .line_info {
          line-height: 30px;
        }
      }
    }
  }

}
</style>



