<template>
  <video-player class="vjs-custom-skin"
                ref="videoPlayer"
                :options="playerOptions"
                :playsinline="true"
                @play="onPlayerPlay($event)"
                @pause="onPlayerPause($event)"
                @ended="onPlayerEnded($event)"
                @loadeddata="onPlayerLoadeddata($event)"
                @waiting="onPlayerWaiting($event)"
                @playing="onPlayerPlaying($event)"
                @timeupdate="onPlayerTimeupdate($event)"
                @canplay="onPlayerCanplay($event)"
                @canplaythrough="onPlayerCanplaythrough($event)"
                @ready="playerReadied"
                @statechanged="playerStateChanged($event)">
  </video-player>
</template>

<script>
// https://github.com/surmon-china/vue-video-player
import 'video.js/dist/video-js.css';
import { videoPlayer, videojs } from 'vue-video-player';

window.videojs = videojs;
require('video.js/dist/lang/zh-CN');

export default {
  name: "vue-video",
  components: {
    videoPlayer
  },
  props: {
    mediaSrc: {
      type: String,
      required: true
    },
    mediaPoster: {
      type: String,
      default: ""
    },
    // showVideo: {
    //   // android
    //   type: Boolean,
    //   default: true
    // },
    autoPlay: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      playerOptions: {
        height: document.body.offsetHeight / 2,
        controls: true, // 
        autoplay: false, // this.autoPlay,
        muted: false, // 
        fluid: true, // 
        language: 'zh-CN',
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 
        sources: [{
          type: "video/mp4",
          // mp4
          src: "" // "http://vjs.zencdn.net/v/oceans.mp4",
          // webm
          // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
        }],
        poster: "" //  "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg"
      }
    };
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player;
    }
  },
  mounted () {
    setTimeout(() => {
      // console.log('dynamic change options', this.player);

      // change src
      // this.playerOptions.sources[0].src = 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm';
      // change item
      // this.$set(this.playerOptions.sources, 0, {
      //   type: "video/mp4",
      //   src: 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm',
      // })
      // change array
      // this.playerOptions.sources = [{
      //   type: "video/mp4",
      //   src: 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm',
      // }]
      this.player.muted(false);
    }, 5000);
  },
  methods: {
    initParam (src, poster) {
      this.$set(this.playerOptions.sources, 0, {
        type: "video/mp4",
        src: src
      });
      this.playerOptions.poster = poster;
    },
    // listen event
    onPlayerPlay (player) {
      // console.log('player play!', player)
    },
    onPlayerPause (player) {
      // console.log('player pause!', player)
    },
    onPlayerEnded (player) {
      // console.log('player ended!', player)
    },
    onPlayerLoadeddata (player) {
      // player.currentTime(0);
      // console.log('player Loadeddata!', player)
    },
    onPlayerWaiting (player) {
      // console.log('player Waiting!', player)
    },
    onPlayerPlaying (player) {
      // console.log('player Playing!', player)
    },
    onPlayerTimeupdate (player) {
      // console.log('player Timeupdate!', player.currentTime())
    },
    onPlayerCanplay (player) {
      // console.log('player Canplay!', player)
    },
    onPlayerCanplaythrough (player) {
      // console.log('player Canplaythrough!', player)
    },
    // or listen state event
    playerStateChanged (playerCurrentState) {
      // console.log('player current update state', playerCurrentState)
    },
    // player is ready
    playerReadied (player) {
      // seek to 10s
      // player.currentTime(10);
      // console.log('example 01: the player is readied', player)
    }
  }
};
</script>

<style lang="less" scoped>
.video-js { /* .video-jsvideo.jsem */
  font-size: 14px;
}

.video-js button {
  outline: none;
}

.video-js.vjs-fluid,
.video-js.vjs-16-9,
.video-js.vjs-4-3 { /*  */
  height: 100%;
  background-color: #161616;
}

.vjs-poster {
  background-color: #161616;
}

/deep/ .video-js .vjs-big-play-button { /*  */
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  /*font-size: 2.5em;*/
  /*line-height: 2.3em;*/
  /*height: 2.5em;*/
  /*width: 2.5em;*/
  /*-webkit-border-radius: 2.5em;*/
  /*-moz-border-radius: 2.5em;*/
  /*border-radius: 2.5em;*/
  /*background-color: rgba(115, 133, 159, .5);*/
  /*border-width: 0.12em;*/
  /*margin-top: -1.25em;*/
  /*margin-left: -1.75em;*/
}

.video-js.vjs-paused .vjs-big-play-button { /*  */
  display: block;
}

.video-js.vjs-error .vjs-big-play-button { /*  */
  display: none;
}

/*.vjs-loading-spinner { !*  *!*/
/*  font-size: 2.5em;*/
/*  width: 2em;*/
/*  height: 2em;*/
/*  border-radius: 1em;*/
/*  margin-top: -1em;*/
/*  margin-left: -1.5em;*/
/*}*/

.video-js .vjs-control-bar { /*  */
  display: flex;
}

.video-js .vjs-time-control {
  display: block;
}

.video-js .vjs-remaining-time {
  display: none;
}

.vjs-button > .vjs-icon-placeholder:before { /* pxem */
  font-size: 22px;
  line-height: 1.9;
}

.video-js .vjs-playback-rate .vjs-playback-rate-value {
  line-height: 2.4;
  font-size: 18px;
}

/*  */
.video-js .vjs-play-progress {
  color: #ffb845;
  background-color: #ffb845;
}

.video-js .vjs-progress-control .vjs-mouse-display {
  background-color: #ffb845;
}

.vjs-mouse-display .vjs-time-tooltip {
  padding-bottom: 6px;
  background-color: #ffb845;
}

.video-js .vjs-play-progress .vjs-time-tooltip {
  display: none !important;
}
</style>



